/* 스타일 리셋 */
@import 'reset.css';
/* 커스텀 디자인 */
:root {
  --color-white : #eee;
  --color-pink : #c89de9;
  --color-blue : #0e3ca5;
  --color-grey : #BABCBE;

  --font-family-Noto : 'Noto Sans KR';
  --font-family-Enjoy : 'OTEnjoystoriesBA';
  --font-family-G-Medium : 'GmarketSansMedium';
  --font-family-G-Bold : 'GmarketSansBold';

  --animation-duration : 300ms;
}
/* 폰트 */
@font-face {
    font-family: 'OTEnjoystoriesBA';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_two@1.0/OTEnjoystoriesBA.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'GmarketSansBold';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansBold.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'GmarketSansMedium';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/GmarketSansMedium.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}


html {
  font-size : 16px;
}

button, input {
  border : none;
  border-radius: 8px;
  outline : none;
  padding: 8px 12px;
  cursor : pointer;
  margin-left: .5rem;
}

button {
  background-color: var(--color-blue);
  color: var(--color-pink);
  transition: var(--animation-duration) opacity;
  font-family: var(--font-family-G-Bold);
}

button:hover {
  opacity: .77;
}

#main-container {
  font-family: var(--font-family-G-Medium);
  width: 100%;
  height: 100vh;
  background-color: var(--color-pink);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 5rem;
}

/* title */
.title {
  font-size: 10rem;
  color: var(--color-blue);
  font-family: var(--font-family-Enjoy);
}

/* made */
.made {
  font-size: 2rem;
  color: var(--color-blue);
  font-family: var(--font-family-Enjoy);
  margin-bottom: 5rem;
}

/* input */
#input-container {
  display: flex;
  align-items: center;
  margin-bottom: 5rem;
}

.input, .submit-btn {
  font-size : 2rem;
}

.input {
  padding-left: 1.5rem;
  color: var(--color-blue);
  background-color: var(--color-white);
}

/* list */
#list-container {
  width: 50%;
  max-height : 300px;
  overflow-y: scroll;
}

.list-items {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--color-white);
  width: 80%;
  padding: .5rem 1rem;
  margin-bottom: 1rem;
  border-radius: 8px;
}

.list-items span {
  color: var(--color-blue);
}